
import Scroller from 'vux/src/components/scroller/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
  components: { Scroller, XHeader },
  data () {
    return {
      isToInit: true,
      currentPage: 1,
      allItems: [],

      pulldownConfig: {
        downContent: '下拉刷新',
        upContent: '释放刷新',
        loadingContent: '加载中...'
      }
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    loadItems: function () {
      this.getData()
    },
    getData: function () {
      this.$http.request({
        method: 'post',
        url: '/I/?method=mc.data.news.list',
        params: {
          direct: 'down',
          num: 50,
          type1: 12,
          p: this.currentPage,
          type2: 22
        }
      })
      .then((response) => {
        console.log('[NewsList getData]接口返回:', response.data)
        this.$refs.ScrollerDom.donePulldown()
        this.allItems = response.data.info
        this.$nextTick(() => {
          this.$refs.ScrollerDom.reset()
        })
      })
      .catch(function (error) {
        console.log(error)
      })
    }
  }
}



